import React from 'react'
import Fade from 'react-reveal/Fade'

export default function Jumbotron() {
    return (
        <section className="jumbotron-section" id="jumbotron-section">
            <div className="jumbotron">
            <Fade top cascade>
                <p className="jumbotron-top">
                    <p>Hi.</p>
                    <p>I am Jakub Moryc,</p>
                    <p>a front-end developer</p>
                    <p>currently learning back-end.</p>
                    <p>I have been learning web dev since 2017.</p>
                </p>
            </Fade>
            <Fade bottom cascade>
                <p className="jumbotron-bot">
                    <p>Below you'll find few of my projects.</p>
                    <p>I invite you to look at them and <a href="#contact-section">get in touch with me</a>.</p>
                    <p>My main framework is React.js and I am currently learning Express.js.</p>
                </p>
            </Fade>
            </div>
        </section>
    )
}
