import React from 'react'
import Fade from 'react-reveal/Fade';

export default function Contact() {
    return (
        <section className="contact-section" id="contact-section">
            <Fade top>
                <h1>Contact me...</h1>
                <p>...and lets work together.</p>
            </Fade>
            <Fade bottom>
                <div className="contact-section-contacts">
                    <div className="contact-section-contact">
                        <div 
                            className="contact-section-contact-icon" 
                            onClick={(e) => {window.location.href = "mailto:mail@example.org"}}
                        >
                            <i class="fas fa-envelope"></i>
                        </div>
                        - <a href="mailto:moryckuba@gmail.com">moryckuba@gmail.com</a>
                    </div>
                    <div className="contact-section-contact">
                        <div 
                            className="contact-section-contact-icon" 
                            onClick={(e) => {window.location.href = "tel:511-625-146"}}
                        >
                            <i class="fas fa-phone"></i>
                        </div>
                        - <a href="tel:511-625-146">511 625 146</a>
                    </div>
                    <div className="contact-section-contact">
                        <div 
                            className="contact-section-contact-icon" 
                            onClick={(e) => {window.open("https://www.linkedin.com/in/jakub-moryc-9446b2198/", "_blank")}}
                        >
                            <i class="fab fa-linkedin"></i>
                        </div>
                        - <a href="https://www.linkedin.com/in/jakub-moryc-9446b2198/" target="_blank" rel="noopener noreferrer">Jakub Moryc</a>
                    </div>
                </div>
            </Fade>
        </section>
    )
}
